const predictDiabetes = async () => {
  const res = await fetch("http://localhost:8000/diabetespredict", {
    method: "POST",
    headers: { "Content-Type": "application/json" },
    body: JSON.stringify({
      age: 45,
      sex: 0,              // 0 = Male, 1 = Female
      bmi: 26.5,
      smoking_history: 1,
      HbA1c_level: 6.2
    })
  });

  const data = await res.json();
  console.log(data);
};
